<template>
  <div @click="clickP" id="myNewp">
    <div v-if="data.seen" class="title">{{data.title}}</div>
    <div v-else style="color: black" class="title">{{data.title}}</div>
    <div class="content">{{data.content}}</div>
    <div class="bottom">
      <a @click.stop="like">
        <img v-show="data.like" src="../common/images/赞@2x.png">
        <img v-show="!data.like" src="../common/images/赞@2x.png">{{data.praise}}
      </a>
      <a> <img src="../common/images/眼@2x.png">{{data.views}}</a>
      <a style="float: right">{{time}}
      </a>
    </div>
  </div>
</template>

<script>
import myUtil from '../common/myUtil'

export default {
  name: 'myNewp',
  props: {
    data: Object
  },
  data () {
    return {
      time: ''
    }
  },
  mounted () {
    this.init()
  },
  methods: {
    init () {
      let time = this.data.time.substring(5, 10)
      let nowTime = myUtil.getTime('MM-DD')
      this.time = nowTime === time ? '今天' + this.data.time.substring(10) : this.data.time.substring(5)
    },
    clickP () {
      this.$emit('clickP', this.data)
      this.$router.push({
        name: 'newS',
        params: {
          news_data: this.data
        }
      })
    },
    like () {
      console.log(this.data)
      if (this.data.like) {
        this.data.like = false
        this.data.praise -= 1
      } else {
        this.data.like = true
        this.data.praise += 1
      }
    }
  }
}
</script>

<style scoped>
  #myNewp {
    border-bottom: 1px solid #999999;
    width: 90%;
    padding: 3% 5%;
    height: 7rem;
    background-color: #FFFFFF;
  }

  #myNewp .title {
    width: 95%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    font-weight: 600;
  }

  #myNewp .content {
    width: 95%;
    overflow: hidden;
    text-overflow: ellipsis;
    margin: 0.5rem 0;
    font-size: 0.95rem;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    overflow: hidden;
    color: #666666;
    -webkit-box-orient: vertical;
    word-break: break-word;
  }

  #myNewp .bottom {
    height: 3rem;
    line-height: 3rem;
    margin-top: 0.5rem;
    border-top: 0.5px solid #EEEEEE;
  }

  #myNewp .bottom a {
    margin: 0 0.1rem;
    text-align: center;
    font-size: 0.9rem;
  }

  #myNewp .bottom a img {
    position: relative;
    top: 0.3rem;
    width: 1.3rem;
  }
</style>



