<template>
  <div id="myTitle">
    <div class="title">{{title}}</div>
    <a ref="title_a" v-show="more_show" @click="clickP">{{more_title}}</a>
    <img @click="clickP" v-show="more_show" :src="img">
  </div>
</template>

<script>
export default {
  name: 'myTitle',
  props: {
    title: String,
    more_show: {
      type: Boolean,
      default: true
    },
    img: {
      type: String,
      default: require('../common/images/箭头.png')
    },
    more_title: String,
    a_size: {
      type: String,
      default: '0.95rem'
    },
    a_red: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      currentS: 0
    }
  },
  mounted () {
    this.init()
  },
  methods: {
    init () {
      let titleA = this.$refs.title_a
      titleA.style.fontSize = this.a_size
    },
    clickP () {
      let titleA = this.$refs.title_a
      if (this.a_red) {
        if (this.currentS === 0) {
          titleA.className += ' active'
          this.currentS = 1
        } else {
          titleA.className = ''
          this.currentS = 0
        }
      }

      this.$emit('clickP')
    }
  }
}
</script>

<style scoped>
  #myTitle {
    border-bottom: 0.5px solid #EEEEEE;
    padding: 0.5rem 1rem;
    position: relative;
  }

  #myTitle .title {
    display: inline-block;
    width: 9rem;
    text-align: left;
    font-size: 1.1rem;
    color: #333333;
    margin: 0.5rem;
    line-height: 1.2rem;
    font-weight: 600;
  }

  #myTitle a {
    float: right;
    line-height: 2rem;
    color: #666666;
    position: absolute;
    right: 2rem;
    font-size: 0.95rem;
  }

  #myTitle img {
    position: absolute;
    right: 1rem;
    top: 1.05rem;
    width: 0.8rem;
  }

  #myTitle .active {
    color: red;
  }
</style>



