<template>
  <div id="myServiceP">
    <div class="myServiceP_left">
      <div class="myServiceP_header" ref="myServiceP_header">
        <img :src="headerImg">
      </div>
      <div class="myServiceP_text">
        <p class="myServiceP_name" ref="myServiceP_name">{{data.name}}</p>
        <p class="myServiceP_concern" v-if="reputablyShow">好评数：{{data.reputably}}</p>
      </div>
    </div>
    <div class="myServiceP_right">
      <div class="right_concern" @click="clickC(data.concerned)">{{data.concerned?'取消关注':'关注'}}</div>
      <div v-show="advisoryShow" class="right_advisory" @click="clickA">咨询</div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'myServiceP',
  props: {
    data: Object,
    advisoryShow: {
      type: Boolean,
      default: true
    },
    reputablyShow: {
      type: Boolean,
      default: true
    },
    headerImg: {
      type: String,
      default: require('../common/images/客服.jpg')
    }
  },
  // watch: {
  //   data: {
  //     handler (newVal, oldVal) {
  //       this.$set(this.data, newVal)
  //     },
  //     immediate: true
  //   }
  // },
  mounted () {
    this.init()
  },
  methods: {
    init () {
      let myServicePName = this.$refs.myServiceP_name
      if (this.data.online) {
        myServicePName.style.color = '#333333'
      } else {
        myServicePName.style.color = '#999999'
      }
    },
    clickC (boo) {
      this.$emit('clickC', boo)
    },
    clickA () {
      this.$store.commit('setCurrentTitle', '咨询')
      this.$router.push({
        name: 'consult',
        params: {
          serviceID: this.data.id
        }
      })
    }
  }
}
</script>

<style scoped>
  #myServiceP {
    height: 5rem;
    width: 100%;
    border-bottom: 0.5px solid #EEEEEE;
    position: relative;
    background-color: #FFFFFF;
  }

  #myServiceP .myServiceP_left {
    display: inline-block;
    height: 5rem;
    position: relative;
    padding: 1rem;
    width: 10rem;
  }

  #myServiceP .myServiceP_left .myServiceP_header {
    width: 3rem;
    height: 3rem;
    border-radius: 1.5rem;
    display: inline-block;
  }

  #myServiceP .myServiceP_left .myServiceP_header img {
    width: 3rem;
    height: 3rem;
    border-radius: 1.5rem;
  }

  #myServiceP .myServiceP_left .myServiceP_text {
    display: inline-block;
    line-height: 2rem;
    position: absolute;
    top: 1.3rem;
    margin: 0 0.5rem;
  }

  #myServiceP .myServiceP_left .myServiceP_name {
    margin: 0 auto;
    font-size: 1.07rem;
    line-height: 1.5rem;
    font-weight: 600;
  }

  #myServiceP .myServiceP_left .myServiceP_concern {
    margin: 0 auto;
    line-height: 1.5rem;
    font-size: 0.8rem;
  }

  #myServiceP .myServiceP_right {
    display: inline-block;
    position: absolute;
    right: 0;
    top: 2rem;

  }

  #myServiceP .myServiceP_right .right_concern {
    display: inline-block;
    margin: 0 0.5rem;
    border: 1px solid #E73E3A;
    border-radius: 0.5rem;
    padding: 0.2rem 0;
    color: #E73E3A;
    width: 4.3rem;
    text-align: center;
  }

  #myServiceP .myServiceP_right .right_advisory {
    display: inline-block;
    margin: 0 0.5rem;
    border: 1px solid #E73E3A;
    border-radius: 0.5rem;
    padding: 0.2rem 1rem;
    color: #E73E3A;
  }
</style>



