<template>
  <div id="myHeader">
    <div class="header_left" @click="back"></div>
    <div class="header_right">{{title}}</div>
  </div>
</template>

<script>
export default {
  name: 'myHeader',
  props: {
    title: String
  },
  methods: {
    back () {
      this.$emit('back')
      let page = ['EvaluationSuccess']
      if (page.includes(this.$store.state.CurrentPage)) {
        this.$router.push({
          name: 'consulting'
        })
        return
      }
      this.$router.go(-1)
    }
  }
}
</script>

<style scoped>
  #myHeader {
    height: 3rem;
    background-color: red;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
  }

  #myHeader .header_left {
    line-height: 4rem;
    margin-top: 1rem;
  }

  #myHeader .header_right {
    display: inline-block;
    width: 19rem;
    text-align: center;
    line-height: 3rem;
    color: #FFFFFF;
    font-size: 1.1rem;
  }
</style>



