<template>

  <div id="myVideop">
    <div class="video">
      <div class="play"  @click="clickP"><img style="width: 3rem" src="../common/images/编组@2x.png"></div>
    </div>
    <div v-if="data.seen"  class="title">{{data.title}}</div>
    <div v-else style="color: black" class="title">{{data.title}}</div>
    <div class="bottom"><a><img src="../common/images/眼@2x.png">{{data.views}}</a>
      <a style="float: right" @click.stop="like">
      <img v-show="data.like" src="../common/images/赞@2x.png">
      <img v-show="!data.like" src="../common/images/赞@2x.png">{{data.praise}}</a>
<!--      <a style="float: right">{{data.time.substring(5)}}</a>-->
    </div>
  </div>
</template>

<script>
export default {
  name: 'myVideop',
  props: {
    data: Object
  },
  mounted () {
  },
  methods: {
    clickP () {
      this.$emit('clickP', this.data)
      this.$router.push({
        name: 'videoS',
        params: {
          videoS_data: this.data
        }
      })
    },
    like () {
      console.log(this.data)
      if (this.data.like) {
        this.data.like = false
        this.data.praise -= 1
      } else {
        this.data.like = true
        this.data.praise += 1
      }
    }
  }
}
</script>

<style scoped>
  #myVideop {
    border-bottom: 1px solid #999999;
    background-color: white;
    height: 16rem;
    padding: 1rem;
  }

  #myVideop .video {
    width: 100%;
    height: 12rem;
    background-color: darkgrey;
    background-image: url("../common/images/背景.png");
    background-repeat: no-repeat;
    background-size: cover;
    border-radius: 10px;
  }

  #myVideop .video .play {
    height: 12rem;
    width: 100%;
    line-height: 15rem;
    text-align: center;
  }

  #myVideop  .title {
    white-space: nowrap;
    padding: 0.5rem 0;
    overflow: hidden;
    text-overflow: ellipsis;
}

#myVideop  .bottom {
}

#myVideop  .bottom a {
  text-align: center;
}
#myVideop  .bottom a img{
  position: relative;
  top: 0.3rem;
  width: 1.4rem;
}
</style>



